import styled from "styled-components";

const SmallRecipeContainer = styled.div`
  text-align: center;
  max-width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 10px auto;
  font-size: 22px;
`;

export default SmallRecipeContainer;



