import styled from "styled-components";

const SearchForm = styled.form`
  margin: 10px auto;
  width: 75%;
  border: solid #454545 2px;
  border-radius: 3px;
  border-top: 25px solid #454545;
  background-color: white;
`;

export default SearchForm;



