import styled from "styled-components";

const ErrorStyles = styled.div`
  p {
    margin: 0;
    font-size:12px;
    color:red;
    font-weight: 100;
  }
  strong {
    margin-right: 1rem;
  }
`;

export default ErrorStyles;


