import styled from "styled-components";

const MultiRecipeButton = styled.button`
  width: 100%;
  font-size: 18px;
  background-color: #8fcfde;
  color: #454545;
  border-radius: 5px;
  :hover {
    background-color:#454545;
    color:#8FCFDE;
  }
`;

export default MultiRecipeButton;



