import styled from "styled-components";

const LandingButton = styled.button`
  display: block;
  margin: 10px auto;
  border-radius: 5px;
  color:#454545;
  width: 100px;
  height: 30px;

  :hover {
    background-color: #454545;
    color: #d3d4d4;
  }
`;

export default LandingButton;



