import styled from "styled-components";

const SignInButton = styled.button`
  margin-top: 10px;
  width: 200px;
  background-color: #8fcfde;
  border-radius: 5px;
  color: white;
  height: 35px;

  :hover {
    color: #8fcfde;
    background-color: #454545;
  }
`;

export default SignInButton;



