import styled from "styled-components";

const Headline = styled.div`
  border-bottom-style: solid;
  border-width: 7px;
  text-align: center;
  border-color: #454545;
  background-color: #8fcfde;
  left: 0;
  right: 0;
  top: 0;
`;

export default Headline;



