import styled from "styled-components";

const Landing = styled.div`
  margin: 40px auto;
  background-color: #8fcfde;
  height: 50vh;
  color: #454545;
  border-radius: 5px;
  max-width: 600px;
  width: 80%;
  border: solid 1px  #454545;
`;

export default Landing;



